body{
    background-color: #091824;
    z-index: -1;
}

.customText_w{
    font-family: 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    font-size: 3.5vh;
    color: white;
}

.customText_w_big{
    font-family: 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    font-size: 4vh;
    color: white;
}

.customText_b{
    font-family: 'Trebuchet MS', Tahoma, Arial;
    text-align: center;
    font-size: 3vh;
    color: black;
}

.Margin_1{
    position: absolute;
    left: 1vh;
    right: 1vh;
    top: 1vh;
    bottom: 1vh;
}

.Margin_2{
    position: absolute;
    left: 2vh;
    right: 2vh;
    top: 2vh;
    bottom: 2vh;
}

.Margin_3{
    position: absolute;
    left: 3vh;
    right: 3vh;
    top: 3vh;
    bottom: 3vh;
}

.std_BTN{
    background-image: linear-gradient(#506371, #374855);
    border-style: solid;
    border-color: #091824; 
} 

.dark_BTN{
    background-image: linear-gradient(#142533, #091824);
    border-style: solid;
    border-color: #091824; 
}

.yellow_BTN{
    background-color: rgb(235, 176, 49);
    border-style: solid;
    border-color: rgb(94, 94, 94); 
}

.green_BTN{
    background-color: rgb(24, 155, 20);
    border-style: solid;
    border-color: rgb(94, 94, 94); 
}

.blue_BTN{
    background-color: rgb(20, 112, 155);
    border-style: solid;
    border-color: rgb(94, 94, 94); 
}

.red_BTN{
    background-color: rgb(170, 11, 11);
    border-style: solid;
    border-color: rgb(94, 94, 94);  
}
.PageBorder{
    background-image: linear-gradient(#21303C, #142533);
    position: absolute;
    left: 1.5vh;
    right: 1.5vh;
    top: 1.5vh;
    bottom: 1.5vh;
}
.Home_BTN{
    width: 90%;
    top: 0;
    height: 40vh;
    margin-top: 2vh;
    margin-left: 5%
}

.Home_BtnBox{
    position: absolute;
    top: 10vh;
    width: 100%;
    bottom: 0;
}

.Home_TitleBox{
    position: absolute;
    width: 100%;
    height: 9vh;
    background-image: linear-gradient(#506371, #374855);
    border-style: solid;
    border-color: #091824; 
}
.Admin_BTN{
    position: relative;
    width: 90%;
    top: 6vh;
    height: 18vh;
    margin-top: 2vh;
    margin-left: 5%
}

.Admin_BtnBox{
    position: absolute;
    top: 13vh;
    width: 100%;
    bottom: 0;
}

.Admin_TitleBox{
    position: absolute;
    width: 100%;
    height: 9vh;
    background-image: linear-gradient(#142533, #091824);
    border-style: solid;
    border-color: #091824; 
}

.Admin_PassForm{
    position: absolute;
    width: 90%;
    left: 5%;
    height: 4vh;
}

.Admin_InfoText{
    width: 90%;
    left: 5%;
    position: relative;
}
